//清浮动
.clearfix() {
  &:before,
  &:after {
    content: '';
    display: table;
  }

  &:after {
    clear: both;
    overflow: hidden;
  }
}

.wrapper {
  width: 100%;
  height: 100%;
  overflow: auto;
}

.header {
  background: #fff;
  border-radius: 4px;
  .clearfix();
  padding: 5px 10px;
}
.title {
  float: left;
  font-size: 16px;
}
.btnBox {
  float: right;
}

.addRportMenu {
  > div {
    height: 36px;
    line-height: 36px;
    cursor: pointer;
    &hover {
      background: #f7f7f7;
    }
  }
}
